@import '../../variables.scss';

.ErrorImg {
    display: block;
    margin: 0 auto;
    width: 400px;
}

.Error h2 {
    text-align: center;
    font-weight: 500;
}

.reloadPage {
    @extend .button-error;

    &:hover {
        color: $color_black;
        background-color: $color_blue;
    }
}


@media (max-width: 600px) {
    .ErrorImg {
        width: 300px;
    }

    .Error h2 {
        text-align: center;
        font-size: 20px;
    }
}