@import '../../variables.scss';

.container {
    display: block;
    margin: 0 auto;
    margin-top: 31px;
    max-width: 1200px;
}

.character {
    display: block;
    margin: 0 auto;

    &_img {
        border: 5px solid #F2F2F7;
        border-radius: 150px;
    }

    &_name {
        text-align: center;
        font-weight: 400;
        font-size: 48px;
        width: 490px;
        margin-top: 5px;
    }
}

.info {
    font-weight: 500;
    font-size: 20px;
    text-align: center;
    letter-spacing: 0.15px;
    color: #8E8E93;
    margin-top: 48px;
    margin-bottom: 48px;
}

.infoWrapper {
    display: block;
    width: 413px;
    margin: 0 auto;


    &_params {
        border-bottom: 1px solid rgb(128, 128, 128, .2);
        margin-bottom: 10px;

        h4 {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            letter-spacing: 0.15px;
            color: #081F32;
        }

        h5 {
            margin-bottom: 11px;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.25px;
            color: #6E798C;
        }

    }
}

.goBack {
    display: flex;
    width: 108px;
    align-items: center;
    justify-content: space-between;
    margin-top: 21px;
    margin-left: 50px;
    transition: .4s ease all;

    h5 {
        font-weight: 700;
        font-size: 18px;
        line-height: 21px;
        color: $color_black;
    }

    &:hover {
        transform: translateX(-10px);

    }
}

@media (max-width: 1500px) {
    .infoWrapper {
        margin-bottom: 150px;
    }
}

@media (max-width: 1200px) {
    .container {
        margin-top: 30px;
    }

    .info {
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .character {
        &_img {
            width: 250px;
        }
    }
}

@media (max-width: 768px) {
    .goBack {
        margin-top: 23px;
        margin-left: 24px;
    }
}

@media (max-width: 600px) {
    .character {
        &_img {
            width: 200px;
        }

        &_name {
            font-weight: 400;
            font-size: 32px;
            line-height: 38px;
            margin-top: 20px;
            width: 280px;
        }
    }

    .infoWrapper {
        width: 285px;
    }

    .info {
        margin-top: 25px;
        margin-bottom: 25px;
    }
}

@media (max-width: 420px) {
    .container {
        margin-top: 77px;
    }

    .info {
        margin-top: 35px;
        margin-bottom: 15px;
        margin-left: 24px;
        text-align: start;
    }

    .character {
        &_name {
            margin-top: 34px;
            margin-bottom: 34px;
        }

        &_img {
            width: 150px;
        }
    }
}