@import '../../variables.scss';

.magnifier {
    width: 24px;
    margin-right: 8px;
    padding: 0px;
    background-color: $color_white;
    border: none;
    outline: none;
    cursor: pointer;
}

.inputWrapper {
    position: relative;
    display: flex;
    align-items: center;
    height: 24px;
    padding: 16px;
    margin: 0 auto;
    margin-bottom: 51px;
    width: 988px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    background-color: $color_white;
    z-index: 1000;
}


@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slideIn {
    from {
        transform: translateY(-75px);
    }

    to {
        transform: translateY(0px);
    }
}

.cardChar {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(4, 240px);
    column-gap: 20px;
    row-gap: 24px;
}

.char {
    &_wrapper {
        opacity: 0;
        background: $color_white;
        box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
        border-radius: 4px;
        transition: .5s ease all;

        &_glow:hover {
            transform: translateY(-10px);
            box-shadow: 0px 0px 0px $color_blue, 0em 0 1.5em $color_blue;
        }
    }
}

.show {
    opacity: 1;
    animation-name: slideIn;
    animation-duration: 3s;
}

.char {
    &_img {
        width: 240px;
        border-radius: 4px 4px 0 0;
        height: 168px;
        object-fit: cover;
    }

    &_descr {
        padding: 12px 16px;
    }

    &_name {
        font-weight: 500;
        font-size: 20px;
        line-height: 30px;
        letter-spacing: 0.15px;
        color: rgba(0, 0, 0, 0.87);
    }

    &_sex {
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        letter-spacing: 0.25px;
        color: rgba(0, 0, 0, 0.6);
    }
}

.logo {
    display: block;
    margin: 0 auto;
    margin-top: 86px;
    margin-bottom: 16px;
}

.wrapper {
    margin-bottom: 188px;
}

@media (max-width: 1200px) {
    .logo {
        margin-top: 25px;
    }

    .cardChar {
        grid-template-columns: (repeat(3, 240px));
    }

    .inputWrapper {
        width: 785px;
    }
}

@media (max-width: 991px) {

    .cardChar {
        grid-template-columns: repeat(2, 240px);
    }

    .inputWrapper {
        width: 666px;
    }
}

@media (max-width: 768px) {
    .logo {
        width: 450px;
        margin-bottom: 20px;
    }

    .inputWrapper {
        width: 440px;
        margin: 0 auto;
        margin-bottom: 32px;
    }
}

@media (max-width: 600px) {
    .logo {
        width: 312px;
        margin-bottom: 32px;
    }

    .cardChar {
        grid-template-columns: repeat(1, 240px);
        row-gap: 32px;
    }

    .inputWrapper {
        width: 312px;
        padding: 16px 0px;
    }

    .magnifier {
        padding-left: 16px;
        padding-right: 20px;
    }
}