@import './variables';

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;

}

input {
  border: none;
  outline: none;
  width: 956px;
  height: 24px;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: rgba(0, 0, 0, 0.5);
}

a {
  text-decoration: none;
  color: $color_black;
}

button {
  outline: none;
  border: none;
}

.BarLoader {
  margin: 0 auto;
  transform: translateY(45vh);
}

.notFoundURL {
  transform: translateY(20vh);

  img {
    display: block;
    margin: 0 auto;
    width: 500px;
  }

  h2 {
    text-align: center;
  }

  a {
    @extend .button-error;

    &:hover {
      background-color: $color_blue;
      color: $color_black;
    }
  }

}


@media (max-width: 1200px) {
  input {
    width: 750px;
  }


  @media (max-width: 991px) {
    input {
      width: 600px;
    }
  }
}

@media (max-width: 768px) {
  input {
    width: 330px;
  }

  .notFoundURL {
    img {
      width: 400px;
    }
  }
}

@media (max-width: 600px) {
  input {
    width: 217px;
  }

  .notFoundURL {
    img {
      width: 300px;
    }
  }
}