$color_white: #fff;
$color_black: #000;
$color_blue: #00fff2;

.button-error {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 30px;
    border-radius: 10px;
    background-color: $color-black;
    margin: 0 auto;
    margin-top: 20px;
    color: $color-blue;
    cursor: pointer;
    transition: 0.5s ease all;
}